@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Anton", sans-serif;
}
.navbar ul li a {
  color: #050038 !important;
  font-weight: 400;
  font-size: 18px;
}
#logo {
  font-weight: 600;
  font-size: 25px;
}
.signbtn {
  background: #0555ff;
  color: #fff;
  border: 0;
  width: 120px;
  border-radius: 20px;
  height: 40px;
}
.signbtn:active {
  background: #fff;
  color: #0555ff;
  border: #0555ff 1px solid;
}
header h1 {
  padding-top: 50px;
  font-size: 96px;
  font-weight: 900;
  text-align: center;
  color: #050038;
}
header h4 {
  text-align: center;
  color: #5b5e6d;
  padding-top: 20px;
}
.leftbtn {
  background: #0555ff;
  border: 0;
  color: #fff;
  width: 208px;
  height: 50px;
  border-radius: 30px;
}
.rightbtn {
  background: #fff;
  color: #0555ff;
  width: 208px;
  height: 50px;
  border: 0;
  border-radius: 30px;
  -webkit-box-shadow: 5px 4px 94px 0px rgba(19, 29, 37, 0.2);
-moz-box-shadow: 5px 4px 94px 0px rgba(19, 29, 37, 0.2);
box-shadow: 5px 4px 94px 0px rgba(19, 29, 37, 0.2);
}
.photomain{
  display: flex;
  margin-top: 100px;
  background: url("https://static.vecteezy.com/system/resources/previews/002/024/418/original/abstract-colorful-geometric-pattern-with-circles-dots-triangles-lines-cross-shapes-on-white-background-vector.jpg") no-repeat center center/cover;
  height: 92vh;
  margin-bottom: 70px;
}
.photomain img{
  margin: auto;
  width: 60%;
}
.item img{
  width: 100px !important;
}
.centr{
  color: #5B5E6D;
  margin-bottom: 40px;
}
#sect1{
  margin-bottom: 50px;
}
.content h1{
color:#050038;
font-weight: 700;
font-size: 42px;
}
.content p{
  color:gray;
  font-size: 20px;
}
.svgbtn{
  width: 245px;
  height: 58px;

  border: 0;
  border-radius: 30px;
  background: rgba(5, 85, 255, 0.1);
  color: #0555ff;
}
.leftContent h1{
  color: #050038;
  font-size: 72px;
  font-weight: 900;
  padding-top: 100px;
}
.leftContent p {
  color: #5B5E6D;
  font-weight: 400;
  font-size: 20px;
  padding-top: 40px;
}
.leftContent a{
  font-size: 18px;
  color: #000;
  font-weight: 500;
  text-decoration: none;
  border-bottom: 2px solid #0555FF;
}
.rightPhoto img{
  width: 100%;
}
.leftPhoto img{
  width: 100%;
}
.rightContent h1{
  color: #050038;
  font-size: 72px;
  font-weight: 900;
  padding-top: 100px;
}
.rightContent p{
  color: #5B5E6D;
  font-weight: 400;
  font-size: 20px;
  padding-top: 40px;
}
.rightContent a{
  font-size: 18px;
  color: #000;
  font-weight: 500;
  text-decoration: none;
  border-bottom: 2px solid #0555FF;
}
#Aside{
  background: #F8F8FA;
  height: max-content;
  padding-bottom: 100px;
  width: 100%;
}
#Aside h6{
  text-align: center;
  color: #5B5E6D;
  padding-top: 100px;
  font-size: 18px;
  font-weight: 400;
}
#Aside h1{
  text-align: center;
  color: #050038;
  font-size: 72px;
  font-weight: 900;
  padding-top: 40px;
}
.comments{
  margin-top: 50px;
}
.comments img{
  width: 100%;

}
footer{
  background: url("https://png.pngtree.com/thumb_back/fw800/background/20191226/pngtree-blue-abstract-background-image_325588.jpg") no-repeat center center/cover;
  height: max-content;
  padding-bottom: 120px;
}
footer h1{
  text-align: center;
  color: #fff;
  font-size: 60px;
  font-weight: 900;
  padding-top: 120px;
}
.footer-div{
  justify-content: center;
}
footer input{
  width: 300px;
  height: 50px;
  border: #050038 1px solid;
  background: #050038;
  padding-left: 20px;
  color: #fff;
  margin-top: 50px;
  border-radius: 10px 0px 0px 10px;
}
footer input:focus{
  outline: none;
}
footer button{
  width: 220px;
  height: 50px;
  background: #050038;
  border: 0;
  border-radius: 0px 10px 10px 0px;
  color: #fff;
  margin-top: 50px;
  font-weight: bold;
}
footer button:active{
  background: #fff;
  color: #050038;
  border: #050038 3px solid;
}
@media  (max-width:1200px){
  .rightContent h1{
    padding-top: 40px !important;
  }
  .leftContent h1{
    padding-top: 40px !important;
  }
}
@media  (max-width:992px){
  .leftContent{
    text-align: center;
  }
  .rightContent{
    text-align: center;
  }
}